import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  Button,
  Group,
  Link,
} from "@vkontakte/vkui";

import { RouletteItem, ModalRouletteResult } from "../gamesItem";
import { ModalRoulette } from "../modal";
import {
  formatCoins,
  SocketManager,
  dataGames,
  formatCoinsToServer,
} from "../helpers";
const { callMethod } = SocketManager;

let interval = null;
let events = false;

const Roulette = ({
  id,
  go,
  connectTwoDevices,
  stub,
  socketReconnect,
  networkStatus,
  updateConnection,
  socketReconnection,
  snackbar,
  pornCoins,
  openBuyPornCoins,
  setActiveModal,
  openAlert,
  openPopoutSharing,
  openMenuVIP
}) => {

  let betAmount = "";
  let globalBank = 0;

  const [dataBet, setDataBet] = useState([]);
  const [bank, setBank] = useState(0);
  const [screenBank, setScreenBank] = useState("0");
  const [timer, setTimer] = useState(300);
  const [screenTimer, setScreenTimer] = useState("00:00");

  function convertTime(sec) {
    return (
      `${Math.floor(sec / 60)}`.padStart(2, 0) +
      ":" +
      `${sec % 60}`.padStart(2, 0)
    );
  }

  useEffect(() => {
    setTimeout(() => {
      callMethod("enterFortune", {
        cancel: false,
      });
    }, 2000);

    SocketManager.socket.on("betsList", (bets) => {
      let b = 0;
      bets.forEach((bet) => {
        b += bet.amount;
      });
      globalBank = b / 1_000;
      setBank(b / 1_000);
      setScreenBank((b / 1_000).toLocaleString());

      setDataBet(
        bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor
          };
        })
      );
    });

    SocketManager.socket.on("summarize", ({ winner, bank }) => {
      if (winner) {
        modalRouletteResult(winner, bank);
      }

      // winner.user.id;
      // winner.user.photoURL;
      // winner.user.fullName;
      // winner.amount;
      // winner.percent;

      getBetsList(1);
    });

    getBetsList(2);
  }, []);

  useEffect(() => {
    if (interval) {
      console.log("–ò–Ω—Ç–µ—Ä–≤–∞–ª —É–∂–µ –µ—Å—Ç—å!");
      clearInterval(interval);
    }

    let seconds = timer;

    setScreenTimer(convertTime(seconds));

    interval = setInterval(() => {
      seconds--;

      if (seconds <= 0) {
        clearInterval(interval);
      }

      setScreenTimer(convertTime(seconds));
    }, 1000);
  }, [timer]);

  function openRouletteModal() {
    setActiveModal(
      <ModalRoulette
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        makeBet={makeBet}
      />
    );
  }

  function getBetsList(a) {
    callMethod("getBetsList", a).then(({ message }) => {
      setTimer(0);
      setTimer(message.timer); // –°–∫–æ–ª—å–∫–æ –æ—Å—Ç–∞–ª–æ—Å—å –¥–æ –∏—Ç–æ–≥–∞ –≤ —Å–µ–∫—É–Ω–¥–∞—Ö

      let b = 0;
      message.bets.forEach((bet) => {
        b += bet.amount;
      });
      setBank(b / 1_000);
      setScreenBank((b / 1_000).toLocaleString());

      setDataBet(
        message.bets.map((bet) => {
          return {
            idVk: bet.user.id,
            name: bet.user.fullName,
            photoUrl: bet.user.photoURL,
            amount: bet.amount,
            isVIP: bet.user.isVIP,
            vipNameColor: bet.user.vipNameColor
          };
        })
      );
    });
  }

  function onChangeAction(e) {
    const value = e.target.value.trim();
    // setBet(+value);
    betAmount = value;
    console.log("value", value);
  }

  const makeBet = () => {
    setActiveModal(null);
    callMethod("addBet", {
      amount: formatCoinsToServer(betAmount),
    }).then(({ status, message }) => {
      
      if (status === 'req_limit') return;
      openAlert(message, status === "denied" ? "red" : "green");
    });
  };

  const renderRoulette = dataBet.map((item, index) => {
    const userUrl = `https://vk.com/id${item.idVk}`;
    const amount = (item.amount / 1_000).toLocaleString();
    const percent = Number(((item.amount / 1_000 / bank) * 100).toFixed(2));

    return (
      <RouletteItem
        key={index}
        id={index + 1}
        name={item.name}
        photoUrl={item.photoUrl}
        userUrl={userUrl}
        amount={amount}
        go={go}
        percent={percent}
        isVIP={item.isVIP}
        vipNameColor={item.vipNameColor}
        openMenuVIP={openMenuVIP}
      />
    );
  });

  const modalRouletteResult = (winner, bank) => {
    // winner.user.id;
    // winner.user.photoURL;
    // winner.user.fullName;
    // winner.amount;
    // winner.percent;
    const userUrl = `https://vk.com/id${winner.user.id}`;
    bank = (bank / 1_000).toLocaleString();

    setActiveModal(
      <ModalRouletteResult
        setActiveModal={setActiveModal}
        onChangeAction={onChangeAction}
        name={winner.user.fullName}
        photoUrl={winner.user.photoURL}
        userUrl={userUrl}
        bank={bank}
        openPopoutSharing={openPopoutSharing}
      />
    );
  };

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "center",
            }}
          >
            <PanelHeaderBack
              onClick={(e) => {
                go(e);

                callMethod("enterFortune", {
                  cancel: true,
                });
                SocketManager.socket.removeEventListener("betsList");
                SocketManager.socket.removeEventListener("summarize");
              }}
              data-to="games"
            />
            {/* <Div
              style={{
                color: "green",
              }}
            >
              {" "}
              –ë–∞–ª–∞–Ω—Å: {formatCoins(durkaCoins)}
            </Div> */}

            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
              onClick={openBuyPornCoins}
            >
              –ü–æ–ø–æ–ª–Ω–∏—Ç—å
            </Button>
            <Button
              style={{
                backgroundColor: "green",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
            >
              {/* 00:{seconds} —Å–µ–∫ */}
              {screenTimer}
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      {connectTwoDevices ? (
        stub(
          "–í—ã –ø–æ–¥–∫–ª—é—á–∏–ª–∏—Å—å —Å 2 —É—Å—Ç—Ä–æ–π—Å—Ç–≤ ",
          socketReconnect,
          "–ü–µ—Ä–µ–ø–æ–¥–∫–ª—é—á–∏—Ç—å—Å—è"
        )
      ) : (
        <>
          {networkStatus ? (
            stub(
              "–û—Ç—Å—É—Ç—Å—Ç–≤—É–µ—Ç –∏–Ω—Ç–µ—Ä–Ω–µ—Ç - —Å–æ–µ–¥–∏–Ω–µ–Ω–∏–µ!",
              updateConnection,
              "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
            )
          ) : (
            <>
              {socketReconnection ? (
                stub(
                  "–û—à–∏–±–∫–∞ –ø–æ–¥–∫–ª—é—á–µ–Ω–∏—è –∫ —Å–µ—Ä–≤–µ—Ä—É",
                  socketReconnect,
                  "–ü–æ–≤—Ç–æ—Ä–∏—Ç—å"
                )
              ) : (
                <Div
                  style={{
                    padding: 20,
                  }}
                >
                  <FixedLayout
                    style={{
                      // display: "flex",
                      // alignItems: "space-between",
                      // flexDirection: "column",
                      // justifyContent: "center",
                      // backgroundColor: "white",
                      // height: "50px",
                      marginTop: 50,
                    }}
                    vertical="top"
                    filled
                  >
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        flexDirection: "column",
                        justifyContent: "center",
                        height: "50px",
                      }}
                    >
                      <Div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "row",
                          justifyContent: "center",
                          color: "green",
                          backgroundColor: "green",
                          borderRadius: "6px",
                          padding: "3px",
                          color: "white",
                          width: "95%",
                          height: "30px",
                          // marginRight: 10,
                        }}
                      >
                        <Div style={{ userSelect: "none" }}>
                          {" "}
                          –ë–∞–ª–∞–Ω—Å: {formatCoins(pornCoins)}
                        </Div>
                        <Div className="iconPornCoin16px" style={{ marginLeft: 5 }} />
                      </Div>
                      {/* <Div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          flexDirection: "column",
                          justifyContent: "center",
                          color: "green",
                          backgroundColor: "green",
                          borderRadius: "6px",
                          color: "white",
                          width: "200px",
                          height: "30px",
                          // marginRight: 10,
                          userSelect: "none"
                        }}
                      >
                        {" "}
                        00:{seconds} —Å–µ–∫
                      </Div> */}
                    </Div>
                  </FixedLayout>

                  <Div
                    style={{
                      marginTop: 30,
                      height: "10px"
                    }}
                  >
                    {/* <Div style={{ marginBottom: "20px" }}>
                      {renderRouletteResult}
                    </Div> */}

                    {renderRoulette ? <>{renderRoulette}</> : ""}
                  </Div>

                  <FixedLayout vertical="bottom" filled>
                    <Div
                      style={{
                        display: "flex",
                        alignItems: "center",
                        flexDirection: "column",
                        justifyContent: "space-evenly",
                        // marginBottom: 5,
                        height: "90px",
                        // backgroundColor: "white",
                      }}
                    >
                      <Div>
                        <Button
                          size="m"
                          onClick={() => openRouletteModal()}
                          style={{
                            backgroundColor: "green",
                            color: "white",
                            borderRadius: "6px",
                            width: "140px",
                            padding: 2
                          }}
                        >
                          –°–¥–µ–ª–∞—Ç—å —Å—Ç–∞–≤–∫—É
                        </Button>
                      </Div>

                      <Div
                        style={{
                          display: "flex",
                          alignItems: "center",
                          justifyContent: "center",
                          flexDirection: "row",
                          color: "white",
                          backgroundColor: "green",
                          width: "95%",
                          padding: "3px",
                          borderRadius: "6px",
                          height: "30px",
                        }}
                      >
                        <Div style={{ userSelect: "none" }}>
                          –í –±–∞–Ω–∫–µ: {screenBank}{" "}
                        </Div>
                        <Div className="iconPornCoin16px" style={{ marginLeft: 5 }} />
                      </Div>
                    </Div>
                  </FixedLayout>
                </Div>
              )}

              {snackbar}
            </>
          )}
        </>
      )}
    </Panel>
  );
};

export { Roulette };
